import permision from "@/common/permission.js"
export default {
	data() {
		return {
			p_brand_index: 0,
			brandicon: '',
		}
	},
	onUnload() {
	},
	methods: {
		chooseImage(){
			uni.chooseImage({
				count:1,
				sizeType:['compressed'],
				success:(res) =>{
					let imgFile = res.tempFilePaths[0];
					console.log("获取的文件路径为:"+imgFile);
					this.brandicon = res.tempFilePaths[0];
				}
			})
		},
		imagepreview(){
			uni.previewImage({
				urls:[this.brandicon],
			})
		}
		
	}
}
